<template>
  <div class="dream_cul">
    <h1>作夢用時刻占</h1>
    結果 : <p >{{result}}</p> 
    <button @click='dream_cul'>計算</button>
  </div>
 
</template>

<script>
export default {

  name: 'dream_cul',
  data () {
    return {
      result: '',
    }
  }, 
  methods : {
    dream_cul(){
      this.result = this.yijing.dream_diviation()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



