<template>
  <div class="now_cul">
    <h1>此刻(小時占)</h1>
    結果 : <p >{{result}}</p>
    <button @click='now_cul'>計算</button>
  </div>
 
</template>

<script>
export default {
  props:{
  },
  name: 'now_cul',
  data () {
    return {
      result: '',
    }
  }, 
  methods : {
    now_cul(){
      this.result = this.yijing.hour_diviation(Date.now())
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '~bootstrap/scss/bootstrap.scss';

h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



