<template>
  <div class="hello">
    <h1>民國生日</h1>
    民國年:
    <select class="custom-select" id="rocYear" v-model="year">
        <option value=0 selected>Choose...</option>
        <option v-for="i in 189" :value="i">{{i}}</option>
    </select>
    月
    <select class="custom-select" id="rocMonth" v-model="month">
        <option value=0 selected>Choose...</option>
        <option v-for="i in 12" :value="i">{{i}}</option>
    </select>    
    日
    <select class="custom-select" id="rocDay" v-model="day">
        <option value=0 selected>Choose...</option>
        <option v-for="i in 31" :value="i">{{i}}</option>
    </select>
    小時
    <select class="custom-select" id="hour" v-model="hour">
        <option  selected>Choose...</option>
        <option v-for="i in 24" :value="i-1">{{i-1}}</option>
    </select>
    結果 : <p >{{result}}</p> 
    <button @click='roc_cul'>計算</button>
  </div>
 
</template>

<script>
export default {
  props:{
    
  },
  name: 'now_cul',
  data () {
    return {
      result: '',
      year:0,
      month:0,
      day:0,
      hour:0
    }
  }, 
  methods : {
    roc_cul(){
      if(this.year !==0 && this.month!==0 && this.day!==0 && typeof(this.hour)!='undefined'){
        var d = new Date();
        this.result = this.yijing.roc_diviation(new Date(this.year,this.month,this.day, this.hour))
      }else{
        alert('請輸入數值')
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped　lang="scss">
@import "~bootstrap/scss/bootstrap";
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



